let baseUrl='dev-95-data-stati-fe4g9e.dev.kdqugou.com/',reg=/^http(s)?:\/\/(.*?)\//;
const u = navigator.userAgent;
const versions = u.split('/');
export default({
    getUrl:function (url){
      if(window.location.hostname=='jsqb.kdqugou.com'){
           return 'http://'+url.replace(reg, 'jsqb.kdqugou.com/')
      }else if(window.location.hostname=='stage.kdqugou.com'){
        return 'http://'+url.replace(reg, 'stage.kdqugou.com/')
      }else{
          return  'http://'+url.replace(reg, baseUrl)
      }
    },
    jump:function (code){
        return nativeMethod.returnNativeMethod('{"skip_code":"'+code+'"}');
    },
    planfrom:function(){
        return {
            isApp: u.indexOf('kdxj') === -1 ? false : true,
            isAndroid: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1, // android终端
            isIos: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios终端])
            isWeixin: u.indexOf('MicroMessenger') > -1, // 是否微信 
            isQQ: u.match(/\sQQ/i) == ' qq' // 是否QQ
        }
    }
})



