<template>
  <div id="app">
    <div class='mobile_wraper'>
      <!-- <p class='time'>活动时间：2017-11-8~2017-11-30</p> -->
      <img src="./assets/open.png" alt="" class='box b1 shake shake-opacity' v-show='!show' @click='open()'>
      <img src="./assets/close.png" alt="" class='box b2' v-show='show'>
      <div class='scroll'>
        <ul class='s'>
          <li v-for='item in data'>{{item.username}}还款成功开出{{item.prize_name}}</li>
        </ul>
      </div>
      <div class='datashow'>注：中奖数据每十分钟更新一次</div>
    </div>
    <div class='btn' @click='go()'>还款拿好礼</div>
    <img src="./assets/our.png" alt=""  class='our' @click='ouropen()'>
    <img src="./assets/role.png" alt="" class='role' @click='roleopen()'>
    <div class='mask' v-show='mask'>
        <div class='pop-box'>
        <div class='pop-header'>活动规则</div>
        <div class='pop-content'>
            1. 活动时间：12月1日—12月31日。<br>
            2. 所有待还款用户均可参与此次活动。<br>
            3. 活动期间内用户单笔订单全额还款即可获得一次开宝箱机会，请在订单状态更新为“已还款”后，再进行开宝箱。<br>
            4. 通过微信服务号“极速888”还款的用户，全额还款后，请打开极速钱包APP进入活动详情页开宝箱。<br>
            5. 奖品设置：iphoneX（每日10位），息费全返，最高2000元临时提额。<br>
            6. 临时提额的有效期为3天，用户获得息费全返资格后本次还款订单的利息和服务费将全额返至用户所绑定的银行卡中。<br>
            7. 获得iphoneX的用户客服人员将会在中奖后3个工作日内联系您，请确保电话的畅通。<br>
            8. 本次活动解释权归极速钱包所有。<br>
        </div>
        <div class='pop-footer' @click='close()'>我知道了</div>
        </div>
    </div>
    <div class='mask' v-show='mask1'>
        <div class='pop-box'>
        <div class='pop-header'>我的奖品</div>
        <div class='pop-content'>
            <p v-if='!mydata.length' style='text-align:center'>您暂时还未获得奖品~</p>
            <table v-if='mydata.length'>
                <tr>
                    <th>开奖时间</th>
                    <th>奖品明细</th>
                </tr>
                <tr v-for='item in mydata'>
                    <td>{{item.created_at}}</td>
                    <td>{{item.prize_name}}</td>
                </tr>
            </table>
        </div>
        <div class='pop-footer' @click='close()'>我知道了</div>
        </div>
    </div>
    <div class='mask' v-show='no'>
        <div class='pop-no'>
            <div class='btnClose' @click='close()'></div>
        </div>
    </div>
    <div class='mask' v-show='get'>
        <div class='pop-get'>
            <div class='text'>{{message}}</div>
            <div class='btnClose' @click='close()'></div>
        </div>
    </div>
    <div class='mask' v-show='tips1'>
        <div class='pop-box'>
        <div class='pop-content'>
            <p style='text-align:center'>您当前订单尚未全额还款成功，按时还款即可开箱拿好礼！</p>   
        </div>
        <div class='pop-footer' @click='go(1)'>去还款</div>
        </div>
    </div>
    <div class='mask' v-show='tips2'>
        <div class='pop-box'>
        <div class='pop-content'>
            <p style='text-align:center'>您当前没有待还订单，借款后按时还款即可开宝箱，领好礼！</p>   
        </div>
        <div class='pop-footer' @click='go(2)'>极速借款</div>
        </div>
    </div>
    <div class='mask' v-show='tips3'>
        <div class='pop-box'>
        <div class='pop-content'>
            <p style='text-align:center'>抱歉，您当前订单还处于逾期状态，无法参与此次活动。下次按时还款即可开箱拿好礼哦！</p>   
        </div>
        <div class='pop-footer' @click='go(3)'>立即还款</div>
        </div>
    </div>
    <!-- <div class='error' v-if='errorflag'>{{error}}</div> -->
    <!-- <router-view/> -->
  </div>
</template>

<script>
import axios from 'axios';
import lib from './lib/lib';
axios.defaults.withCredentials = true;
export default {
  name: 'active',
  data () {
    return {
        error:'错误信息',
        no:false,
        get:false,
        mask1:false,
        show:false,
        mask:false,
        data:[],
        mydata:[],
        message:'',
        luck_draw_usable:0,
        user_status:0,
        errorflag:false,
        tips1:false,
        tips2:false,
        tips3:false
    }
  },
  mounted () {
    this.getData();
    setInterval(function(){
        $('.s').animate({top:'-0.6rem'},1000,function(){
            $('.s').css({top:0});
            var clone = $($('.s li')[0]);
            $($('.s li')[0]).remove();
            $('.s').append(clone);
        });
    },1500)
    $('.s').animate({top:'-0.6rem'},1000);
  },
  computed:{
      count(){
          return this.$store.state.count;
      }
  },
  methods:{
    open (){
        let that = this;
        if(this.luck_draw_usable>0){
            axios.get(lib.getUrl('http://dev-91-add-source-mplwym.dev.kdqugou.com/newh5/web/act/hkbx?request_type=luck_draw'))
            .then(function(data){
                if(data.data.code==0){
                    that.show = true;
                    that.message = data.data.message;
                    setTimeout(function(){
                        that.get = true;
                        that.show = false;
                    },100)
                }else{
                    that.errorflag = true;
                    that.error = data.data.message;
                    setTimeout(function(){
                        that.errorflag = false;
                    },1000)
                }
            })
        }else{
            if(this.user_status==3){
                this.tips1 = true;
            }else if(this.user_status==2 || this.user_status==1){
                this.tips2 = true;
            }else if(this.user_status==4){
                this.tips3 = true;
            }
        }
     
    },
    roleopen (){
        this.mask = true;
    },
    ouropen (){
        this.mask1 = true;
        this.getData();
        
    },
    getData (){
        let that = this;
        axios.get(lib.getUrl('http://dev-91-add-source-mplwym.dev.kdqugou.com/newh5/web/act/hkbx?request_type=get_record_list'))
        .then(function(data){
            that.mydata = data.data.my_prize_list;
            that.data = data.data.prize_list;
            that.user_status = data.data.user_status;
            that.luck_draw_usable = data.data.luck_draw_usable;
            if(that.user_status==0){
                return lib.jump(106);
            }
        })
    },
    close (){
        this.no=false;
        this.get=false;
        this.mask = false;
        this.mask1 = false;
    },
    go (n){
        if(n){
            if(n==1){
                return lib.jump(102);
            }else if(n==2){
                if(this.user_status==1){
                    return lib.jump(107);
                }else if(this.user_status==2){
                    return lib.jump(101);
                }
            }else if(n==3){
                return lib.jump(102);
            }
        }else{
            if(this.user_status==0){
                return lib.jump(106);
            }else if(this.user_status==1){
                return lib.jump(107);
            }else if(this.user_status==2){
                return lib.jump(101);
            }else {
                return lib.jump(102);
            }
        }
        
    }
  }
}
</script>

<style lang='scss' scoped>
*{
  padding:0;
  margin:0;
}
.error{
    position: fixed;
    top:50%;
    left: 50%;
    width:320px;
    text-align:center;
    line-height:60px;
    color:#fff;
    transform: translate(-50%,-50%);
    -webkit-transform:translate(-50%,-50%);
    background:rgba(0,0,0,.8);
    border-radius:6px;
}
.datashow{
    box-sizing: border-box;
    position:absolute;
    top:1130px;
    width:100%;
    text-align:left;
    color:#FBDA41;
    padding:0px 50px;
}
.time{
    position: absolute;
    top:345px;
    width:100%;
    text-align: center;
    color:#FBDA41;
}
.btnClose{
    position: absolute;
    bottom:0;
    width:300px;
    height: 85px;
    left:50%;
    transform:translate(-50%,0);
    -webkit-transform:translate(-50%,0);
}
.pop-no{
  width:554px;
  height:361px;
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  -webkit-transform:translate(-50%,-50%);
  background:url(./assets/2.png) no-repeat 0 0;
  background-size:100% 100%;
}
.pop-get{
  width:623px;
  height:440px;
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  -webkit-transform:translate(-50%,-50%);
  background:url(./assets/1.png) no-repeat 0 0;
  background-size:100% 100%;
  .text{
      width:320px;
      text-align: center;
      position: absolute;
      left: 230px;
      top:160px;
      color:#FF5E4C;
      font-size:40px;
  }
}
.pop-box{
  width:540px;
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  -webkit-transform:translate(-50%,-50%);
  .pop-header{
      line-height: 100px;
      color:#FFF;
      font-size:40px;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
      background:#2C0C64;
  }
  .pop-content{
      background: #fff;
      text-align:left;
      padding:20px;
      max-height:500px;
      overflow: hidden;
      overflow-y:auto;
      -webkit-overflow-scrolling : touch; 
      font-size:30px;
      p{
          font-size:32px;
      }
      table{
          width:100%;
          text-align: center;
          tr{
              width:50%;
          }
          th{
              text-align: center;
              font-weight: 600;
              font-size:30px;
          }
      }
  }
  .pop-footer{
      line-height: 100px;
      color:#FFF;
      font-size:40px;
      border-bottom-left-radius: 12px;
      border-bottom-right-radius: 12px;
      background:#fff;
      color:#2C0C64;
      border-top:1px solid #efefef;
  }
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-bottom:constant(safe-area-inset-bottom);
  background:#EA2648;
}
.s{
    text-align:left;
}
.mobile_wraper{
  height:1536px;
  background:url(~assets/jsqbProject/activity/openbox2/assets/bg.jpg) no-repeat 0 0;
  background-size:100% 100%;
  margin-bottom:88px;
}
.btn{
  height:90px;
  text-align:center;
  color:#fff;
  font-size:40px;
  font-weight:400;
  line-height:90px;
  background:#EA2648;
  position:fixed;
  bottom:0;
  bottom:constant(safe-area-inset-bottom);
  left:0;
  width:100%;
}
.scroll{
  box-sizing:border-box;
  position:absolute;
  top:950px;
  width:100%;
  height:160px;
  padding:0px 70px;
  overflow:hidden;
  ul{
    position:relative;
    li{
      color:#fff;
      height:41px;
      line-height:41px;
      font-size:32px;
    }
  }
}
.box{
  position:absolute;
  left:50%;
}
.b1{
  width:399px;
  height:359px;
  top:450px;
  margin-left:-200px;
}
.b2{
  width:492px;
  height:389px;
  top:440px;
  margin-left:-246px;
}
.role{
  position:fixed;
  top:330px;
  right:0;
  height:55px;
  width:186px;
}
.our{
  position:fixed;
  top:330px;
  left:0;
  height:55px;
  width:186px;
}
.mask{
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:rgba(0,0,0,.8);
}
@-webkit-keyframes shake-opacity {
    0% {
        -webkit-transform: translate(0px, 0px) rotate(0deg);
      
    }
    10% {
        -webkit-transform: translate(-4px, 1px) rotate(-1.5deg);
      
    }
    20% {
        -webkit-transform: translate(-4px, -4px) rotate(-2.5deg);
      
    }
    30% {
        -webkit-transform: translate(-3px, -5px) rotate(-1.5deg);
         }
    40% {
        -webkit-transform: translate(0px, 0px) rotate(0.5deg);
      
    }
    50% {
        -webkit-transform: translate(0px, 0px) rotate(-2.5deg);
      
    }
    60% {
        -webkit-transform: translate(-5px, -2px) rotate(1.5deg);
      
    }
    70% {
        -webkit-transform: translate(-1px, 3px) rotate(-0.5deg);
      
    }
    80% {
        -webkit-transform: translate(-2px, -2px) rotate(1.5deg);
      
    }
    90% {
        -webkit-transform: translate(-3px, -3px) rotate(1.5deg);
      
    }
}
@-ms-keyframes shake-opacity {
    0% {
        -ms-transform: translate(0px, 0px) rotate(0deg);
      
    }
    10% {
        -ms-transform: translate(0px, -4px) rotate(-1.5deg);
      
    }
    20% {
        -ms-transform: translate(-2px, 0px) rotate(0.5deg);
         }
    30% {
        -ms-transform: translate(-1px, 1px) rotate(-2.5deg);
      
    }
    40% {
        -ms-transform: translate(-2px, 4px) rotate(-1.5deg);
      
    }
    50% {
        -ms-transform: translate(-4px, 2px) rotate(-2.5deg);
      
    }
    60% {
        -ms-transform: translate(3px, 0px) rotate(0.5deg);
         }
    70% {
        -ms-transform: translate(-4px, -5px) rotate(-0.5deg);
         }
    80% {
        -ms-transform: translate(4px, -1px) rotate(1.5deg);
      
    }
    90% {
        -ms-transform: translate(4px, 0px) rotate(1.5deg);
      
    }
}
@keyframes shake-opacity {
    0% {
        transform: translate(0px, 0px) rotate(0deg);
      
    }
    10% {
        transform: translate(-2px, -1px) rotate(-0.5deg);
      
    }
    20% {
        transform: translate(-4px, 4px) rotate(1.5deg);
      
    }
    30% {
        transform: translate(-4px, -1px) rotate(-1.5deg);
      
    }
    40% {
        transform: translate(-2px, -1px) rotate(-2.5deg);
      
    }
    50% {
        transform: translate(-4px, 1px) rotate(-2.5deg);
      
    }
    60% {
        transform: translate(-2px, 4px) rotate(0.5deg);
      
    }
    70% {
        transform: translate(-3px, 1px) rotate(-0.5deg);
      
    }
    80% {
        transform: translate(0px, 0px) rotate(-0.5deg);
      
    }
    90% {
        transform: translate(2px, -1px) rotate(-2.5deg);
      
    }
}
.shake {
    display: inline-block;
    -webkit-transform-origin: center center;
    -ms-transform-origin: center center;
    transform-origin: center center
}
.shake.shake-opacity {
    -webkit-animation-name: shake-opacity;
    -ms-animation-name: shake-opacity;
    animation-name: shake-opacity;
    -webkit-animation-duration: 800ms;
    -ms-animation-duration: 800ms;
    animation-duration: 800ms;
    -webkit-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
    -ms-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-delay: 0s;
    -ms-animation-delay: 0s;
    animation-delay: 0s;
    -webkit-animation-play-state: running;
    -ms-animation-play-state: running;
    animation-play-state: running
}
</style>



