import axios from 'axios'

export function fetchGet(url) {
    return new Promise((resolve, reject) => {
        axios.get(url)
            .then(response => {
                 alert('Api--ok');
                resolve(response.data);
            })
            .catch((error) => {
              console.log(error)
               reject(error)
            })
    })
}
export function fetchPost(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
            .then(response => {
                 alert('Api--ok');
                resolve(response.data);
            })
            .catch((error) => {
              console.log(error)
               reject(error)
            })
    })
}


export default {
  // 获取我的页面的后台数据
  mineBaseMsgApi() {
     alert('进入api.js')
    return fetchGet('http://dev-91-add-source-mplwym.dev.kdqugou.com/newh5/web/act/hkbx?request_type=luck_draw');
  }
}


